
.main-content{
  overflow: scroll;
}
.layout{
    background: #f5f7f9;
    position: relative;
    height: 100%;
}
.layout-breadcrumb{
    padding: 10px 15px 0;
}
.layout-content{
    min-height: 200px;
    margin: 15px;
    /*overflow: hidden;*/
    border-radius: 4px;
}
.layout-content-main{
    padding: 10px;
}
.layout-copy{
    text-align: center;
    padding: 10px 0 20px;
    color: #9ea7b4;
}
.layout-menu-left{
    background: #464c5b;
}
.layout-header{
    height: 60px;
    background: #fff;
    box-shadow: 0 1px 1px rgba(0,0,0,.1);
}
.layout-logo-left{
    width: 90%;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    margin: 15px auto;
}
.layout-ceiling-main a{
    color: #9ba7b5;
}
.layout-hide-text .layout-text{
    display: none;
}
.ivu-col{
    transition: width .2s ease-in-out;
}
.ivu-row-flex{
  height: 100%;
}
.ivu-badge-dot{
  box-shadow: none;
  margin-left: 40px;
  right: -10px;
}
.ivu-avatar-string{
  position: static !important;
}
.avatar-dosen{
  color: white;
  left: -5px;
  background-color: #19be6b;
}
.avatar-kaprodi{
  color: white;
  left: -5px;
  background-color: #ed3f14;
}
.avatar-akademik{
  color: white;
  left: -5px;
  background-color: #1c2438;
}
.avatar-mahasiswa{
  color: white;
  background-color: #2d8cf0;
}
.ivu-breadcrumb{
  text-transform: capitalize;
}
.menu-user .ivu-menu-item-active{
  border-bottom: none !important;
}
.menu-user .ivu-menu-item>a>i{
  margin-right: 6px;
}
.menu-user .ivu-menu-item>a, .menu-user .ivu-menu-item>i{
  color: #495060;
}
.menu-user .ivu-select-dropdown .ivu-menu-item-selected{
  background-color: transparent !important;
}
.ivu-menu-horizontal .ivu-menu-submenu .ivu-select-dropdown .ivu-menu-item-selected, .ivu-menu-horizontal .ivu-menu-submenu .ivu-select-dropdown .ivu-menu-item-selected:hover{
  color: #495060;
}



